import '../styles/Calculadora.scss'
import { useState } from 'react';


export function Page(){
  const [ result, setResult] = useState('');


  const checkValues = (e) => {
    setResult(result.concat(e.target.name));
  };

  const clear = () => {
    setResult("")
  };

  const handleRemove = () => {
    setResult(result.slice(0, -1));
  };

  const getResult = () => {
    try {
      setResult(eval(result).toString());
    } catch (error) {
      console.log("Error")
    }
  };

  return (
    <div className="calculadora">
      <form className="text-container">
        <input type="text" value={result} disabled/>
      </form>

      <div className="container">

        <button type="button" onClick={handleRemove}>C</button>
        <button type="button" onClick={clear}>AC</button>
        <button name="/" className="calc" type="button" onClick={checkValues} >%</button>
        <button name="*" className="calc" type="button" onClick={checkValues} >x</button>

        <button name="7" type="button" onClick={checkValues} >7</button>
        <button name="8" type="button" onClick={checkValues} >8</button>
        <button name="9" type="button" onClick={checkValues}>9</button>
        <button name="-" className="calc" type="button" onClick={checkValues}>-</button>

        <button name="4" type="button" onClick={checkValues}>4</button>
        <button name="5" type="button" onClick={checkValues}>5</button>
        <button name="6" type="button" onClick={checkValues}>6</button>
        <button name="+" className="calc" type="button" onClick={checkValues}>+</button>

        <button name="1" type="button" onClick={checkValues}>1</button>
        <button name="2" type="button" onClick={checkValues}>2</button>
        <button name="3" type="button" onClick={checkValues}>3</button>
        <button name="=" className="calc" type="button" onClick={getResult} 
         id="equal">=</button>

        <button name="0" type="button" onClick={checkValues} 
          id="zero" >0</button>
        <button name="."type="button" onClick={checkValues}>.</button>

      </div>
    </div>
  )
};